.modal{
    position: fixed;
    top:0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0,0,0,0.4);
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal__content{
    background: white;
    border-radius: 4px;
    padding: 10px 20px;
}
